/* Hero Container */
.hero-container {
  position: relative;
  height: 80vh;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Animated Background */
.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.floating-shapes {
  position: absolute;
  width: 100%;
  height: 100%;
}

.shape {
  position: absolute;
  border-radius: 50%;
  background: linear-gradient(45deg, rgba(59, 130, 246, 0.1), rgba(16, 185, 129, 0.1));
  animation: float 20s infinite ease-in-out;
}

.shape-1 {
  width: 300px;
  height: 300px;
  top: 10%;
  left: 10%;
  animation-delay: 0s;
}

.shape-2 {
  width: 200px;
  height: 200px;
  top: 60%;
  right: 15%;
  animation-delay: 5s;
}

.shape-3 {
  width: 150px;
  height: 150px;
  bottom: 20%;
  left: 20%;
  animation-delay: 10s;
}

.shape-4 {
  width: 250px;
  height: 250px;
  top: 30%;
  right: 30%;
  animation-delay: 15s;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
    opacity: 0.3;
  }
  25% {
    transform: translateY(-30px) rotate(90deg);
    opacity: 0.6;
  }
  50% {
    transform: translateY(-60px) rotate(180deg);
    opacity: 0.3;
  }
  75% {
    transform: translateY(-30px) rotate(270deg);
    opacity: 0.6;
  }
}

/* Background Images */
.slide-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 1.5s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 2;
}

.slide-background.active {
  opacity: 1;
}

.slide-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transform: scale(1.1);
  transition: transform 8s ease-out;
}

.slide-background.active .slide-image {
  transform: scale(1);
}

.slide-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(0,0,0,0.4), rgba(0,0,0,0.7));
}

/* Hero Content */
.hero-content {
  position: relative;
  z-index: 10;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.content-container {
  max-width: 1200px;
  padding: 0 2rem;
  width: 100%;
}

/* Main Title Section */
.main-title-section {
  margin-bottom: 4rem;
  position: relative;
}

.main-title {
  font-size: 4.5rem;
  font-weight: 900;
  color: white;
  line-height: 1.1;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 20px rgba(0, 0, 0, 0.5);
  letter-spacing: -0.02em;
}

.title-word {
  display: inline-block;
  margin-right: 0.5rem;
  animation: titleReveal 0.8s ease-out forwards;
  opacity: 0;
  transform: translateY(50px);
}

.title-word:nth-child(1) { animation-delay: 0.1s; }
.title-word:nth-child(2) { animation-delay: 0.2s; }
.title-word:nth-child(3) { animation-delay: 0.3s; }
.title-word:nth-child(4) { animation-delay: 0.4s; }
.title-word:nth-child(5) { animation-delay: 0.5s; }
.title-word:nth-child(6) { animation-delay: 0.6s; }

@keyframes titleReveal {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.title-underline {
  width: 200px;
  height: 4px;
  background: linear-gradient(90deg, #3b82f6, #10b981, #f59e0b, #ef4444);
  margin: 0 auto;
  border-radius: 2px;
  animation: underlineExpand 1s ease-out 0.8s forwards;
  transform: scaleX(0);
}

@keyframes underlineExpand {
  to {
    transform: scaleX(1);
  }
}

/* Slide Content */
.slide-content-wrapper {
  position: relative;
  min-height: 400px;
}

.slide-content {
  animation: slideContentIn 1s cubic-bezier(0.4, 0, 0.2, 1) forwards;
  opacity: 0;
  transform: translateY(30px);
}

@keyframes slideContentIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.content-badge {
  display: inline-block;
  padding: 0.5rem 1.5rem;
  border-radius: 50px;
  color: white;
  font-weight: 600;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin-bottom: 1.5rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  animation: badgePulse 2s infinite;
}

@keyframes badgePulse {
  0%, 100% {
    transform: scale(1);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 6px 30px rgba(0, 0, 0, 0.4);
  }
}

.slide-title {
  font-size: 3.5rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  line-height: 1.1;
  text-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
  background: linear-gradient(45deg, currentColor, rgba(255, 255, 255, 0.8));
  background-clip: text;
  -webkit-background-clip: text;
}

.slide-description {
  font-size: 1.3rem;
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.7;
  max-width: 800px;
  margin: 0 auto 2rem;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
}

.stats-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 0.75rem 1.5rem;
  border-radius: 50px;
  color: white;
  font-weight: 600;
  margin-bottom: 2.5rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
}

.stats-icon {
  font-size: 1.2rem;
}

/* CTA Section */
.cta-section {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.btn-primary,
.btn-secondary {
  position: relative;
  padding: 1rem 2.5rem;
  border-radius: 50px;
  font-weight: 700;
  font-size: 1.1rem;
  cursor: pointer;
  border: none;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  text-transform: uppercase;
  letter-spacing: 0.05em;
  min-width: 200px;
}

.btn-primary {
  color: white;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.btn-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.4);
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.5);
  transform: translateY(-3px);
}

.btn-ripple {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

.btn-primary:active .btn-ripple,
.btn-secondary:active .btn-ripple {
  width: 300px;
  height: 300px;
}

/* Navigation Controls */
.navigation-controls {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  z-index: 20;
  pointer-events: none;
}

.nav-arrow {
  position: absolute;
  top: 0;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 1rem;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  pointer-events: all;
  overflow: hidden;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-arrow:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.1);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.nav-left {
  left: 2rem;
}

.nav-right {
  right: 2rem;
}

.arrow-icon {
  width: 24px;
  height: 24px;
  z-index: 2;
}

.nav-ripple {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

.nav-arrow:active .nav-ripple {
  width: 100px;
  height: 100px;
}

/* Play/Pause Control */
.play-pause-btn {
  position: absolute;
  top: 5rem;
  right: 2rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 0.75rem;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 20;
  overflow: hidden;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
}

.play-pause-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.1);
}

.control-icon {
  width: 20px;
  height: 20px;
  z-index: 0;
}

.control-ripple {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

.play-pause-btn:active .control-ripple {
  width: 80px;
  height: 80px;
}

/* Advanced Slide Indicators */
.slide-indicators {
  position: absolute;
  bottom: 3rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 1rem;
  z-index: 20;
}

.indicator {
  position: relative;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 25px;
  padding: 0.75rem 1.5rem;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(10px);
  overflow: hidden;
  color: white;
  font-weight: 600;
  font-size: 0.9rem;
}

.indicator:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
}

.indicator.active {
  background: rgba(255, 255, 255, 0.2);
  border-color: var(--indicator-color);
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.3);
}

.indicator-progress {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 3px;
  border-radius: 0 0 25px 25px;
  transition: width 0.1s linear;
}

.indicator-label {
  position: relative;
  z-index: 2;
}

/* Progress Ring */
.progress-ring {
  position: absolute;
  bottom: 2rem;
  right: 2rem;
  width: 80px;
  height: 80px;
  z-index: 20;
}

.progress-svg {
  width: 100%;
  height: 100%;
  transform: rotate(-90deg);
}

.progress-bg {
  fill: none;
  stroke: rgba(255, 255, 255, 0.2);
  stroke-width: 3;
}

.progress-fill {
  fill: none;
  stroke-width: 3;
  stroke-linecap: round;
  stroke-dasharray: 283;
  transition: stroke-dashoffset 0.1s linear, stroke 0.3s ease;
}

.progress-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-weight: 700;
  font-size: 0.9rem;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .main-title {
    font-size: 3.5rem;
  }
  
  .slide-title {
    font-size: 2.8rem;
  }
  
  .slide-description {
    font-size: 1.2rem;
  }
}

@media (max-width: 768px) {
  .main-title {
    font-size: 2.5rem;
    margin-bottom: 2rem;
  }
  
  .slide-title {
    font-size: 2.2rem;
  }
  
  .slide-description {
    font-size: 1.1rem;
    padding: 0 1rem;
  }
  
  .content-container {
    padding: 0 1rem;
  }
  
  .nav-left {
    left: 1rem;
  }
  
  .nav-right {
    right: 1rem;
  }
  
  .slide-indicators {
    bottom: 2rem;
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  
  .indicator {
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
  }
  
  .cta-section {
    flex-direction: column;
    gap: 1rem;
  }
  
  .btn-primary,
  .btn-secondary {
    width: 100%;
    max-width: 280px;
  }
}

@media (max-width: 480px) {
  .main-title {
    font-size: 2rem;
  }
  
  .slide-title {
    font-size: 1.8rem;
  }
  
  .slide-description {
    font-size: 1rem;
  }
  
  .nav-arrow {
    width: 50px;
    height: 50px;
    padding: 0.75rem;
  }
  
  .arrow-icon {
    width: 20px;
    height: 20px;
  }
  
  .progress-ring {
    width: 60px;
    height: 60px;
  }
  
  .progress-text {
    font-size: 0.8rem;
  }
}

/* carousel end */

/* chatbot start */
.chatbot-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: linear-gradient(135deg, #ff4ecd, #6b5bff);
  color: white;
  border-radius: 50%;
  padding: 14px;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(0,0,0,0.2);
  transition: 0.3s;
  z-index: 9999;
}
.chatbot-button:hover { transform: scale(1.1); }

.chatbot-window {
  position: fixed;
  bottom: 80px;
  right: 20px;
  width: 320px;
  max-height: 450px;
  display: flex;
  flex-direction: column;
  background: #fff;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 5px 20px rgba(0,0,0,0.3);
  animation: slideUp 0.3s ease;
  z-index: 9999;
}

.chatbot-header {
  background: linear-gradient(135deg, #ff4ecd, #6b5bff);
  padding: 12px;
  color: white;
  font-weight: bold;
  text-align: center;
}

.chatbot-messages {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
}

.chatbot-message {
  padding: 8px 12px;
  margin-bottom: 8px;
  border-radius: 10px;
  max-width: 80%;
}
.chatbot-message.user {
  background: #d1e7ff;
  align-self: flex-end;
}
.chatbot-message.bot {
  background: #f3f0ff;
  align-self: flex-start;
}

.chatbot-input-container {
  display: flex;
  border-top: 1px solid #ddd;
}
.chatbot-input-container input {
  flex: 1;
  border: none;
  padding: 10px;
  outline: none;
}
.chatbot-input-container button {
  background: #6b5bff;
  border: none;
  color: white;
  padding: 0 12px;
  cursor: pointer;
}

@keyframes slideUp {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

@media (max-width: 500px) {
  .chatbot-window {
    width: 90%;
    right: 5%;
  }
}



/* chatbot end */
