.en-floating-btn {
  position: fixed;
  right: 20px;
  bottom: 20px;
  background: linear-gradient(45deg, #ff9800, #ff5722);
  background: linear-gradient(135deg, #ff4ecd, #6b5bff);

  color: white;
  padding: 14px 20px;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  font-size: 16px;
  z-index: 999;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  animation: en-pulse 1.5s infinite;
}

.en-floating-btn {
  position: fixed;
  right: 25px;
 
  bottom: 50%;
  transform: translateY(-50%);

  background: linear-gradient(135deg, #ff4ecd, #6b5bff);

  color: white;
  padding: 12px 20px;
  font-weight: bold;
  writing-mode: vertical-rl;
  text-orientation: mixed;
  border-radius: 8px  8px;
  cursor: pointer;
  z-index: 1000;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}
@keyframes en-pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

.en-form-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.en-form-container {
  background: white;
  padding: 25px;
  border-radius: 15px;
  width: 90%;
  max-width: 400px;
  animation: en-swing 1s ease-out;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
}

@keyframes en-swing {
  0% { transform: rotate(5deg) translateY(-100vh); }
  50% { transform: rotate(-5deg) translateY(30px); }
  70% { transform: rotate(3deg) translateY(-10px); }
  100% { transform: rotate(0) translateY(0); }
}

.en-form-title {
  text-align: center;
  margin-bottom: 15px;
  color: #ff5722;
}

.en-input, .en-textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 8px;
  border-radius: 8px;
  border: 1px solid #ccc;
  outline: none;
  font-size: 14px;
}

.en-input:focus, .en-textarea:focus {
  border-color: #ff9800;
}

.en-textarea {
  height: 80px;
  resize: none;
}

.en-submit-btn {
  background: linear-gradient(45deg, #4caf50, #388e3c);
  color: white;
  padding: 12px;
  width: 100%;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 15px;
}

.en-error {
  font-size: 12px;
  color: red;
  display: block;
  margin-bottom: 5px;
}


