/* about page styling */
.about-container {
  font-family: 'Segoe UI', sans-serif;
  color: #222;
}

/* Banner */
.about-banner {
  height: 50vh;
  width: 100%;
  background-image: url('./Assets/resin.jpg'); /* Change to your image path */
  background-size: cover;
  background-position: center;
  position: relative;
}

.about-banner-overlay {
  background: rgba(0, 0, 0, 0.5);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-banner-text {
  color: white;
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
  padding: 0 15px;
}

/* About Content */
.about-content {
  padding: 40px 20px;
  max-width: 1200px;
  margin: auto;
}

.about-heading {
  display: flex;
  align-items: center;
  font-size: 2rem;
  margin-bottom: 20px;
  color: #4a4a4a;
}

.about-icon {
  margin-right: 10px;
  color: violet;
}

.about-description {
  font-size: 1.1rem;
  line-height: 1.7;
  margin-bottom: 40px;
}

/* Mission & Vision */
.about-mission-vision {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 40px;
}

.mv-card {
  flex: 1 1 300px;
  background: linear-gradient(135deg, violet, #090978);
  color: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
}

.mv-card h3 {
  display: flex;
  align-items: center;
  font-size: 1.3rem;
  margin-bottom: 10px;
}

.mv-card h3 svg {
  margin-right: 8px;
}

/* Highlights */
.about-highlights {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.highlight-card {
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
  padding: 20px;
  flex: 1 1 300px;
  max-width: 350px;
  text-align: center;
  transition: transform 0.3s ease;
}

.highlight-card:hover {
  transform: translateY(-8px);
}

.highlight-icon {
  font-size: 2.5rem;
  color: #1F2661;
  margin-bottom: 10px;
}

.highlight-card h3 {
  margin-bottom: 10px;
  font-size: 1.3rem;
}

.highlight-card p {
  font-size: 1rem;
  color: #555;
}

/* Responsive */
@media (max-width: 768px) {
  .about-banner-text {
    font-size: 1.8rem;
  }
}


